.loader-block {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  opacity: 0;
  width: 0.5em;
  height: 0.5em;
  background: #fdfdfd;
  animation: show 0.88s step-end infinite alternate, pulse 0.88s linear infinite alternate; }

.loader-block:nth-child(1) {
  transform: translate(0, 0);
  animation-delay: 0.065s; }

.loader-block:nth-child(2) {
  transform: translate(0.66667em, 0);
  animation-delay: 0.13s; }

.loader-block:nth-child(3) {
  transform: translate(1.33333em, 0);
  animation-delay: 0.195s; }

.loader-block:nth-child(4) {
  transform: translate(0, 0.66667em);
  animation-delay: 0.325s; }

.loader-block:nth-child(5) {
  transform: translate(0.66667em, 0.66667em);
  animation-delay: 0.13s; }

.loader-block:nth-child(6) {
  transform: translate(1.33333em, 0.66667em);
  animation-delay: 0.455s; }

.loader-block:nth-child(7) {
  transform: translate(0, 1.33333em);
  animation-delay: 0.39s; }

.loader-block:nth-child(8) {
  transform: translate(0.66667em, 1.33333em);
  animation-delay: 0.26s; }

.loader-block:nth-child(9) {
  transform: translate(1.33333em, 1.33333em); }

@-webkit-keyframes pulse {
  from,
  40% {
    background: #fdfdfd; }
  to {
    background: #000; } }

@-webkit-keyframes show {
  from, 40% {
    opacity: 0; }
  41%, to {
    opacity: 1; } }

